import React, { FC } from 'react';
import { ColumnsProps, DefaultSortProps, getExtraData, OrderType, referenceColumn, SearchDataGrid, SearchViewProps, WindowComponent } from 'summer';
import { CLASS_NAME, ValorImpostoPLR } from '../../../../models/dtos/mfp/valorImpostoPLR';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referenciaInicial',
    order: OrderType.DESC,
  },
];

const ValorImpostoPLRSearchView: FC<SearchViewProps<ValorImpostoPLR>> = props => {
  const columns: ColumnsProps[] = [
    referenceColumn({
      align: 'center',
      dataField: 'referenciaInicial',
      editable: false,
      headerAlign: 'center',
      sort: true,
      text: 'Referencia Inicial',
    }),
  ];
  return (
    <WindowComponent title="Manutenção de Valor do PLR Anual">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ValorImpostoPLREditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ValorImpostoPLRSearchView;
